import React from 'react';
import {Link} from 'react-router';

class PartialBioOneLiner extends React.Component {
  constructor(props) {
    super(props);
  }

  render () {
    return (
        <div>
          <p>
              is an automation advocate, web developer, productivity and gamification enthusiast, amateur pianist, guitarist, and composer.
          </p>
          <div className="bio-navigation">
            <ul className="links bio-links row">
                <li className="col-xs-3 text-align-left">
                    <Link to="/" title="Home Page"><i className="fa fa-chevron-left"></i></Link>
                </li>
                <li className="col-xs-6">&nbsp;</li>
                <li className="col-xs-3 text-align-right">
                    <Link to="/bio/multiline" title="Multiple Lines Biography"><i className="fa fa-battery-half"></i></Link>
                </li>
            </ul>
          </div>
        </div>
      );
  }
}

module.exports = PartialBioOneLiner;


