import React from 'react';
import {render} from 'react-dom';
import {Router, Route, IndexRoute, Link, hashHistory} from 'react-router';

// scss
require('bootstrap-sass/assets/stylesheets/_bootstrap.scss')
require('../css/common/fonts.scss');
require('../css/common/style.scss');
require('../css/public/style.scss');


// Font Awesome
require("font-awesome-sass-loader");

// Partials
var PartialHome = require('./partials/partial-home.jsx');
var PartialBio = require('./partials/partial-bio.jsx');
var PartialBioOneLiner = require('./partials/partial-bio-one-liner.jsx');
var PartialBioMultiLine = require('./partials/partial-bio-multi-line.jsx');
var PartialBioFull = require('./partials/partial-bio-full.jsx');
var PartialMusic = require('./partials/partial-music.jsx');

const AppSections = {
  'Home': PartialHome,
  'Bio': PartialBio,
  'Music': PartialMusic
};

class OsbertNgokWebsiteApp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      section: 'Home'
    };
  }

  changeSection(section) {
    this.setState(AppSections[section] ? {
      section: section
    } : {
      section: 'Home'
    })
  }

  getSubComponent() {
    return AppSections[this.state.section] || PartialHome;
  } 

  render() {
    return (
      <div className="container ui-view-container">
        <Router history={hashHistory}>
          <Route path="/">
            <IndexRoute component={PartialHome}></IndexRoute>
            <Route path="bio" component={PartialBio}>
              <IndexRoute component={PartialBioOneLiner}></IndexRoute>
              <Route path="multiline" component={PartialBioMultiLine}></Route>
              <Route path="full" component={PartialBioFull}></Route>
            </Route>
            <Route path="music" component={PartialMusic}></Route>
          </Route>
        </Router>
      </div>);
  }
}

render((
    <OsbertNgokWebsiteApp/>
), document.getElementById('app'));


