import React from 'react';
import $ from 'jquery';

const ccImg = require('../../img/common/by-nc-sa.png')

class MusicListDetail extends React.Component {
  constructor(props) {
    super(props);
  }

  getYear() {
    return 1900 + new Date(this.props.date).getYear();
  }
}

class MusicList extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      children: []
    };

    var _this = this;
    // Get json
    $.ajax({
      "method": "GET",
      "url": "/json/music.json",
      "dataType": "json"
    }).then(function successCallback(response) {
      _this.setState({
        children: response.data.map(song => new MusicListDetail(song))
      });
    }, function errorCallback(response){
      _this.setState({
        children: []
      });
    });
  }
  render() {
    return (
      <div>
        <a rel="license" href="https://creativecommons.org/licenses/by-nc-sa/3.0/">
          <img alt="Creative Commons License" className="border-none" src={ccImg} />
        </a>
        {
          this.state.children.filter((detail) => detail.props.favourate).map((detail) =>
          (
            <div className="music-list-detail" key={detail.props.id}>
              <div className="music-list-detail-title">
                <span>{detail.props.title}</span>&nbsp;{
                detail.props.score &&
                <a href={detail.props.score}><i className="fa fa-book"></i></a>
              }
                <span>(<span>{detail.getYear().toString()}</span>)</span>
              </div>
              <audio className="music-list-detail-audio" controls preload="metadata">
                <source src={detail.props.mp3} type="audio/mpeg" />
                Your browser does not support the audio element.
              </audio>
            </div>)
          )
        }
      </div>
    );
  }
}

class PartialMusic extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return <div>
            <div className="place-holder col-lg-4 col-md-4 col-sm-3 hidden-xs-down"></div>
            <div className="center-locator col-lg-4 col-md-4 col-sm-6 col-xs-12">
              <br className="clear" />
              <h1>My Compositions</h1>
              <MusicList />
            </div>
            <div className="place-holder col-lg-4 col-md-4 col-sm-3 hidden-xs-down"></div>
          </div>;
  }
}

module.exports = PartialMusic;


