import React from 'react';

class PartialBio extends React.Component {
  constructor(props) {
    super(props);
  }

  render () {
    return <div>
            <div className="place-holder col-lg-4 col-md-4 col-sm-3 hidden-xs-down"></div>
            <div className="center-locator col-lg-4 col-md-4 col-sm-6 col-xs-12">
              <br className="clear" />
              <h1 className="name">Osbert Ngok</h1>
              {this.props.children}
            </div>
            <div className="place-holder col-lg-4 col-md-4 col-sm-3 hidden-xs-down"></div>
          </div>;
  }
}

module.exports = PartialBio;


